@font-face {
    font-family: Voyager Light;
    src: url(./voyager/Webfonts/Voyager\ Light\ Webfont.woff);
}

body {
    margin:0;
    padding:0;
    height:100%;
    width:100%;
    font-family: 'Voyager Light';
    background-color: black;
}


/* HEADER */
.header {
    width: 100%;
    height: 115px;
    align-items: center;
    margin-top: 0px;
    display: flex;
    box-shadow: 0px 5px 25px black;
    border: none;
    background-color: rgb(0, 0, 0);
    
}
.title {
    padding: 20px;
    margin-left: 60px;
    font-size: 2em;
    letter-spacing: 2px;
    color: white;
    font-family: 'Voyager Light';
    font-size: 1.5em;
}

/* .app {

} */


/* MAIN CONTENT */
.main {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-top: 0;
}

#video {
    position: fixed;
    z-index: -1;
    width: 100%;
    filter: brightness(95%);
}

/* PHOTO CONTENT */
.photo-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    margin-top: 100px;
    width: 600px;
    /* filter: drop-shadow(2px 2px 10px #000000) */
}

.photo {
    width: 500px;

}

/* INFO CONTENT */
.info-container {
    padding-top: 20px;
    width: 750px;
    margin-bottom: auto;
    margin-top: 25px;
}

.info-title {
    color: white;
    font-size: 50px;
    text-shadow: 4px 4px #000000;
}

.date {
    color: white;
    font-size: 15spx;
}

.description {
    padding: 30px;
    font-size: 17px;
    color: white;
    font-family: sans-serif;
    line-height: 1.3;
    letter-spacing: .5px;

    background-color: rgba(5, 4, 4, 0.377);
    backdrop-filter: blur(5px);
    filter: drop-shadow(8px 10px 4px #000000)
}


/* Media Query References */

/* 1312px */
/* 992px */
/* 688 */


@media all and (max-width: 1312px) {

    .title {
        font-size: 1.2em;
    }

    .info-container {
        margin-top: 5px;
    }

    .info-title {
        color: white;
        font-size: 2.5rem;
        margin-left: 35px;
    }

    .date {
        margin-left: 35px;  
    }

    .description {
        width: 30rem;
        margin-left: 35px;
    }

    .photo-container {
        margin-top: 20px;
        justify-content: center;
    }
}

@media all and (max-width: 1156px) {
    .title {
        font-size: 1.1em;
        margin-left: 20px;
    }

    .info-container {
        margin-top: 5px;
        margin-left: 25px;
    }

    .info-title {
        color: white;
        font-size: 2.2rem;
        margin-left: 35px;
    } 


    .description {
        width: 30rem;
        font-size: .9rem;
    }

    .photo-container {
        margin-right: 80px;
        margin-top: 80px;
    }

    .photo {
        width: 350px;
    }
}

@media all and (max-width: 992px) {
    .title {
        font-size: 1.1em;
        margin-left: 20px;
    }

    .info-container {
        margin-top: 5px;
        margin-left: 70px;
    }

    .info-title {
        color: white;
        font-size: 1.8rem;
        padding-right: 20px;
        margin-left: 40px;
    } 

    .description {
        width: 28rem;
        font-size: .8rem;
    }

    .photo-container {
        margin-right: 185px;
        margin-top: 80px;
    }

    .photo {
        width: 300px;
    }
}

